.airing__section {
    margin-top: 2rem;
}
.airing__container {
    padding: 1rem;
    row-gap: 1rem;
    display: flex;
}

.airing__anime {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    padding: 1rem;
    width: 100%;
    display: grid;
    gap: 1rem;
    background-color: var(--container-color);
    border: 3px solid rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    object-fit: cover;
}

.airing__img {
    width: 300px;
    height: 250px;
    border-radius: 3px;
    border: 3px solid rgba(0, 0, 0, 0.15);
}

.airing__anime-title {
    text-align      : center;
    color           : var(--title-color-dark);
    font-size       : var(--smaller-font-size);
    font-weight     : var(--font-semi-bold);
}