.title {
    padding         : 1rem;
    text-align      : center;
    font-size       : var(--h1-font-size);
    color           : var(--title-color-dark);
}

.details{
    background-color : var(--container-color);
    border           : 3px solid rgba(0, 0, 0, 0.15);
    border-radius    : 20px;
    margin-right     : 2rem;
    margin-left      : 2rem;
    padding          : 1rem;
}

.detail {
    grid-template-columns   : repeat(2, 1fr);
    display                 : grid;
}

.anime__details {
    display         : flex;
    flex-direction  : column;
    justify-content : space-between;
    font-size       : var(--normal-font-size);
}

.anime__property {
    display         : flex;
    gap             : 1rem;
    color           : var(--text-color);
}

.span__property {
    font-weight     : var(--font-medium);
    color           : var(--title-color);
}

.image {
    justify-self: center;

}
.image__details {
    border-radius   : 7px;
    width           : 300px;
    height          : 350px;
}

.description {
    margin-top  : 2rem;
    text-align  : justify;
    font-size   : var(--small-font-size);
    color       : var(--text-color);
}

button {
    background-color    : transparent;
    cursor              : pointer;
    font-weight         : var(--font-medium);
}

.trailer {
    padding     : 2rem;
    text-align  : center;
    font-size   : var(--h1-font-size);
    color       : var(--title-color-dark);
}

.trailer__container {
    display         : flex;
    justify-content : center;
    align-items     : center;
}

.iframe {
    display          : flex;
    outline          : none;
    background-color : var(--container-color);
    border           : 3px solid rgba(0, 0, 0, 0.15);
    border-radius    : 5px;
    padding          : .75rem;
    margin-bottom    : 1rem;
}

.not__avail {
    text-align       : center;
    width            : 75%;
    outline          : none;
    background-color : var(--container-color);
    border           : 3px solid rgba(0, 0, 0, 0.15);
    border-radius    : 10px;
    padding          : .75rem;
    margin-bottom    : 1rem;
}

.home__btn {
    position            : absolute;
    margin-top          : 1rem;
    margin-left         : 2rem;
    padding             : .25rem .5rem;
    border-radius       : 25px;
    background-color    : var(--container-color);
    border              : 3px solid rgba(0, 0, 0, 0.15);
    font-size           : var(--small-font-size);
    color               : var(--title-color-dark);
}

.home__btn:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

/* Breakpoints */
/* Medium Device */
@media screen and (max-width: 768px) { 
    .title {
        font-size   : var(--h2-font-size);
    }

    .details{
        margin-right     : .25rem;
        margin-left      : .25rem;
        padding          : .5rem;
    }

    .anime__details {
        font-size       : var(--small-font-size);
    }

    .anime__property {
        gap             : .25rem;
    }

    .image__details {
        margin-top      : .5rem;
        width           : 240px;
        height          : 250px;
    }

    .description {
        margin-top  : 1rem;
        font-size   : var(--smaller-font-size);
    }

    button {
        font-size   : var(--smaller-font-size);
    }

    .trailer {
        padding     : 1rem;
        font-size   : var(--h2-font-size);
    }

    .home__btn {
        margin-left         : .5rem;
        font-size           : var(--smaller-font-size);
    }
}

/* Medium Device */
@media screen and (max-width: 576px) {
    
    .title {
        font-size   : var(--h3-font-size);
    }

    .details{
        margin-right     : .1rem;
        margin-left      : .1rem;
        padding          : .3rem;
    }

    .anime__property {
        font-size       : var(--smaller-font-size)
    }

    .image__details {
        width           : 200px;
        height          : 220px;
    }

    .description {
        margin-top  : .5rem;
    }

    .iframe {
        height          : 350px;
    }

    .home__btn {
        position            : absolute;
        margin-top          : .75rem;
    }
}

/* Small Device */
@media screen and (max-width: 480px) {

    .details{
        margin-right     : .5rem;
        margin-left      : .5rem;
        padding          : .5rem;
    }

    .detail {
        grid-template-columns   : repeat(1, 3fr);
        display                 : grid;
        gap: 1rem;
    }

    .anime__property {
        font-size       : var(--small-font-size);
    }

    .image__details {
        width           : 275px;
        height          : 300px;
    }

    .iframe {
        height          : 300px;
    }
}
