.sidebar__anime {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    padding: 0.5rem;
    display: grid;
    background-color: var(--container-color);
    border: 3px solid rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    object-fit: cover;
}

.sidebar__title {
    text-align      : center;
    color           : var(--title-color-dark);
    font-size       : var(--h3-font-size);
    font-weight     : var(--font-semi-bold );
    margin-bottom   : .5rem;
}

.sidebar__img {
    width           : 200px;
    height          : 200px;
    border-radius   : 3px;
    border          : 3px solid rgba(0, 0, 0, 0.15);
}

.sidebar__anime-title {
    text-align      : center;
    color           : var(--title-color-dark);
    font-size       : var(--smaller-font-size);
    font-weight     : var(--font-semi-bold);
    margin-bottom   : 1rem;
}


/* Breakpoints */
/* Medium Device */
@media screen and (max-width: 768px) { 
    .sidebar__anime {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    }
    .sidebar__img {
        width           : 150px;
        height          : 150px;
        border-radius   : 3px;
        border          : 3px solid rgba(0, 0, 0, 0.15);
    }
}

/* Medium Device */
@media screen and (max-width: 576px) { 
    .sidebar__anime {
        grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    }
    .sidebar__img {
        width           : 120px;
        height          : 120px;
        border-radius   : 3px;
        border          : 3px solid rgba(0, 0, 0, 0.15);
    }
    .sidebar__title {
        font-size       : var(--normal-font-size);
    }
}

/* Small Device */
@media screen and (max-width: 350px) { 
    .sidebar__anime {
        display: none;
    }
}