.header {
    padding-top         : 2rem;
}

.logo {
    display         : flex;
    align-items     : center;
    justify-content : center;
    margin-bottom   : 2rem;
}

.search__container {
    display         : flex;
    align-items     : center;
    justify-content : center;
    gap             : 1.75rem;
    
}

.popular__btn,
.airing__btn,
.upcoming__btn {
    padding             : .25rem 2rem;
    outline             : none;
    border-radius       : 25px;
    background-color    : var(--container-color);
    border              : 3px solid rgba(0, 0, 0, 0.15);

}

.submit__btn {
    padding             : .25rem;
    margin-left         : .25rem;
    outline             : none;
    border-radius       : 10px;
    background-color    : var(--container-color);
    border              : 2px solid rgba(0, 0, 0, 0.15);
}

.popular__btn:hover,
.airing__btn:hover,
.upcoming__btn:hover,
.submit__btn:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

.input__control input {
    padding         : .25rem 0rem;
    border-radius   : 10px;
    border          : 2px solid rgba(0, 0, 0, 0.15);
}

/* Breakpoints */
/* Large Device */
@media screen and (max-width: 992px) { 
    .search__container {
        gap             : 1.5rem;  
    }
}

/* Medium Device */
@media screen and (max-width: 768px) { 
    .search__container {
        gap: 1rem;   
    }
    
    .popular__btn,
    .airing__btn,
    .upcoming__btn {
        padding             : .25rem .5rem;
    }
}

@media screen and (max-width: 576px) {    
    .logo {
        display         : flex;
        align-items     : center;
        justify-content : center;
        margin-bottom   : 1rem;
    }

    .popular__btn,
    .airing__btn,
    .upcoming__btn {
        margin-bottom: 1rem;

    }

    .search__form {
        position: absolute;
        margin-top: 4.5rem;
    }
}